body {
  margin: 0;
  padding: 0;
  background-color: white;
}
.App-header {
  background-color:orangered;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  color: white;
}
.App-content {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.App-bell-wrapper {
  width: 40px;
  height: 40px;
  background-color: orangered;
  position: absolute;
  right: 0;
  margin-right: 24px;
}