body {
    font-family: 'JosefinSans-Regular';
}
.request-products {
    width: 100%;
    height: 300px;
    background-color: white;
    margin: 4px;
    border-radius: 10px;
}
.request-products-header {
    width: 100%;
    height: 100px;
    display: flex;
    padding: 24px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box; 
    box-sizing: border-box;
    
}
.request-products-header-name {
    height: 48px;
    float: left;
    flex-grow: 1;
    line-height: 2;
    font-size: 22px;
}
.request-products-header-quantity {
    height: 48px;
    width: 48px;
    background-color: orangered;
    float: right;
    border-radius: 50%;
    font-weight: bold;
    font-size: 24px;
    line-height: 2;
    color: white;
    text-align: center;
}
.request-products-content {
    width: 100%;
    overflow-y: scroll;
    float: left;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.card {
    width: 300px;
    margin: 10px;
    border-radius: 10px;
    border:1px solid #e6e6e6;
    box-shadow:0 0 3px #dcdcdc;
    transition:all .2s ease-in-out;
    background-color: white;
}
.card:hover {
    box-shadow:0 3px 22px rgba(0,0,0,.1);
    transform:translateY(-5px);
}
.card .username {
    width: 100%;
    height: 40px;
    background: rgba(242, 245, 249, 0.94);
    font-weight: bold;
    font-size: 20px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    line-height: 2;
    font-family: 'JosefinSans-Bold';
    color: #424a52;
    text-align: center;
    
}
.green {
    border-bottom: 3px solid green;
}
.orange {
    border-bottom: 3px solid yellow;
}
.red {
    border-bottom: 3px solid red;
}
.card .content {
    width: 100%;
    height: 400px;
    
}
.card .content p {
    color: black;
    font-family: 'JosefinSans-Regular';
    padding-left: 10px;
    margin: 0px;
    height: 24px;
    text-transform: uppercase;
    line-height: 2;
}
.card .content .more {
    padding-left: 10px;
    font-family: 'JosefinSans-Italic';
    color: orangered;
}
.card .footer {
    width: 100%;
    height: 50px;
    background: rgba(242, 245, 249, 0.94);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
}
.card > .footer > .info {
    width: 70%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'JosefinSans-Light';
    font-size: 20px;

}
.card > .footer > .eye {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 5px;
    padding-left: 5px;
    padding-right: 5px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box; 
    box-sizing: border-box;
}
.card > .footer > .eye img {
    width: 30px;
    height: 30px;
}

.card > .footer > .printer {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 5px;
    padding-left: 5px;
    padding-right: 5px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box; 
    box-sizing: border-box;
}
.card > .footer > .printer:hover, .card > .footer > .eye:hover {
    cursor: pointer;
    background-color: silver;
    border-radius: 10px;
}
.card > .footer > .printer img {
    width: 30px;
    height: 30px;
}
.card > .footer > .eye:hover {
    cursor: pointer;
}
.check {
    text-decoration: line-through;
    background-color: rgb(161 204 130);
    color: black !important;   
}
.cancel {
    text-decoration: line-through;
    background-color: rgb(74 74 74);
    color: white !important;
    
}