
.header {
    width: 100%;
    height: 60px;
    background: rgba(242, 245, 249, 0.94);
    color: #424a52;
    text-align: center;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
}
.back {
    background-color: rgb(139, 138, 172);
    border-radius: 0px 0px 10px 0px;
    padding: 15px;
}
.back::before {
    content: '<';
    color: white;
    padding-right: 10px;
}
.back a {
    color: white;
    text-decoration: none;
}
.body {
    width: 100%;
}
table {
    width: 100%;
    border: 1px solid black;
    border-collapse: collapse;
}
table thead tr th {
    background-color: rgb(162, 172, 182);
    padding: 10px;
    color: white;
    text-align: left;
}
table tr td {
    border: 1px solid rgb(231, 231, 231);
    padding: 10px;
    font-family: 'JosefinSans-Light';
    font-size: 18px;
    font-weight: lighter;
    text-transform: uppercase;
}
.menu {
    position: absolute;
    width: 300px;
    background-color: silver;
    margin: 1px;
    display: none;
    border-radius: 20px;
    border: 2px solid silver;
    padding-bottom: 5px;

}
.menu .header {
    width: 100%;
    height: 40px;
    border-radius: 16px 16px 0px 0px;
    display: flex;
    justify-content: center;
    align-content: center;
}
.menu .header .name {
    width: calc(100% - 32px);
    height: 100%;
    line-height: 2.5;
    font-size: 14px;
    font-family: 'JosefinSans-Bold';
}
.menu .header .close {
    width: 32px;
    height: 32px;
    margin-right: 5px;
}
.menu .header .close img {
    width: 32px;
    height: 32px;
}
.menu .header .close:hover {
    cursor: pointer;
}
.menu .body {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box; 
    box-sizing: border-box;
    padding: 20px;
}
.menu .body input {
    border: 1px solid silver;
    border-radius: 40px;
    height: 30px;
    width: 90%;
    text-align: center;
    font-size: 18px;
}
.menu .body input:focus {
    outline: none;
    border: 1px solid orangered;
}
.menu .footer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.menu .footer .error{
    background-color: red;
    padding: 5px;
    color: white;
    font-family: "Arial";
    border-radius: 10px;
    margin-top: 10px;
    font-size: 14px;
}

.menu .footer button {
    margin: 4px;
    background-color: orangered;
    color: white;
    padding: 20px;
    border: none;
    border-radius: 50px;   
    outline: none;
    font-family: 'JosefinSans-Regular';
}
.menu .footer button:hover {
    background-color: rgb(206, 55, 0);
    cursor: pointer;
}
.menu .footer button.negative {
    background-color: rgb(66, 66, 66);
}
.menu .footer button.negative:hover {
    background-color: rgb(37, 37, 37);
    cursor: pointer;
}
.check {
    text-decoration: line-through;
    background-color: rgb(161 204 130);
    
}
.cancel {
    text-decoration: line-through;
    background-color: rgb(74 74 74);
    color: white;
    
}