body, html {
    font-family: 'JosefinSans-Bold';
}


.login-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
}
.login {
    width: 300px;
    border: 1px solid rgb(207, 207, 207);
    border-radius: 20px;
    
}
.login .header {
    background: rgba(242, 245, 249, 0.94);
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #424a52;
    border-radius: 20px 20px 0px 0px;

}
.login .body {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box; 
    box-sizing: border-box;
}
.login .body input {
    border: 1px solid silver;
    border-radius: 40px;
    height: 40px;
    width: 90%;
    text-align: center;
    font-size: 24px;
    font-family: 'JosefinSans-Regular';
}
.login .body input:focus {
    outline: none;
    border: 1px solid orangered;
  }
.login .footer {
    width: 100%;
    padding: 10px;
    background-color: rgb(245, 245, 245);
    border-radius: 0px 0px 20px 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box; 
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
}
.login .footer button {
    background-color: orangered;
    color: white;
    padding: 20px;
    border: none;
    border-radius: 50px;   
    outline: none;
}
.login .footer button:hover {
    background-color: rgb(206, 55, 0);
    cursor: pointer;
}